.repo-card-div {
  color: rgb(88, 96, 105);
  background-color: rgb(255, 255, 255);
  border: 1px solid;
  /* box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px -15px; */
  border-radius: 10px;
  padding: 2rem;
  height: 10rem;

  -webkit-transition: all 1s;

  transition: all 1s;


}
.repo-card-div:hover {
  /* box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px -10px; */
  /* z-index: 1; */
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
  border: 2px solid;

  /* border: 1px solid; */
  border-color: #55198b;
  /* box-shadow: #55198b; */

}

.repo-stats {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: rgb(106, 115, 125);
}

.repo-left-stat {
  flex-grow: 1;
  display: flex;
}

.language-color {
  width: 10px;
  height: 10px;
  background-color: blue;
  margin-right: 0.25rem;
  border-radius: 100%;
}

.repo-left-stat span {
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
}

.repo-name-div {
  display: flex;
  align-items: center;
}

.repo-svg {
  margin-right: 0.5rem;
  min-width: 16px;
}

.repo-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  color: rgb(36, 41, 46);
  margin-bottom: 0.75rem;
  font-size: 25px;
  font-weight: 700;
  letter-spacing: -0.5px;
  overflow: hidden;
  margin: 0px;
}

.repo-star-svg {
  margin-right: 0.3rem;
}

.repo-description {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
